body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Personal Website Styles */
html {
  font-family: "Work Sans", sans-serif;
  width: 100vw;
  height: 100vh;
  max-width: 100%;
}

* {
  box-sizing: border-box;
  color: black;
  padding: 0;
  margin: 0;
}

*:hover {
  transition: 0.3s;
}

header {
  height: 5rem;
  width: inherit;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  justify-content: center;
  margin: auto;
}

main {
  margin-top: 10rem;
  justify-content: center;
  width: inherit;
}

/* CSS Rules for projects page */
#projects-main {
  width: 90%;
  display: inline-flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-left: 5%;
}

#projects-main h1 {
  text-align: center;
  flex-basis: 100%;
  margin-top: -6rem;
}

#projects-main h2 {
  flex-basis: 100%;
  margin: 2rem 0 1rem 0;
  text-align: center;
}

.project-heading {
  text-decoration: none;
  margin: 2rem 0 2rem 0;
  text-align: center;
  font-size: 2rem;
  color: #304e7e;
}

.project-image {
  width: 30rem;
  height: 15rem;
  border: 2px solid black;
  margin-bottom: -2rem;
}

.project-image:hover {
  opacity: 0.7;
}

.project-container {
  width: 30rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: 0 auto;
  padding: 1rem;
}

.project-container ul {
  height: 6rem;
}

.mb-3 {
  margin: 2rem 5rem 0 5rem;
}

#contact-info {
  display: flex;
  margin-top: 8rem;
  flex-wrap: wrap;
  width: 70%;
  margin: 0 auto;
}

#contact-options {
  margin-left: 5ch;
}

#contact-text {
  margin-top: 5rem;
}

.heading-info {
  text-align: center;
  font-size: 24px;
  display: inline-flex;
  width: 100%;
  margin-top: 5rem;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.main-text {
  margin-right: 2rem;
}

.heading-info img {
  align-self: center;
  height: 300px;
  width: 300px;
  border-radius: 100%;
  border: 2px solid #304e7e;
}

.heading-info h3 {
  color: gray;
  font-size: 20px;
  margin-bottom: 2rem;
}

.main-navigation {
  grid-area: 1 / 1 / 1 / 1;
  display: inline-flex;
  margin: auto;
  margin-right: 1rem !important;
}
.main-navigation li {
  list-style-type: none;
  margin: 0 10px 0 10px;
  text-decoration: none;
  border: 1px solid white;
  padding: 8px;
  background-color: #90d1ff;
  border: 2px solid #304e7e;
}
.main-navigation li:hover {
  background-color: #77c7ff;
}

li,
a {
  color: black !important;
}

#view-projects {
  background-color: #90d1ff;
  padding: 10px 32px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  font-family: "Work Sans", sans-serif;
  cursor: pointer;
  margin-bottom: 3rem;
  border: 2px solid #304e7e;
}

#view-projects:hover {
  background-color: #77c7ff;
}

/* Bootstrap Dropdown */
.dropdown {
  top: 25%;
  display: none;
  justify-self: flex-end;
  margin-right: 2rem;
  border-radius: 0 !important;
}

#dropdown-basic {
  background-color: #90d1ff;
  color: black;
  border-radius: 0;
  border: 2px solid #304e7e;
  padding: 10px 16px;
  min-width: 10rem;
}

#dropdown-basic:hover {
  background-color: #77c7ff;
}

#dropdown-basic:focus {
  box-shadow: none;
}

/* Styles for contact us form*/
input[type="text"],
select,
textarea {
  width: 80%;
  padding: 12px;
  border: 1px solid #304e7e;
  margin-top: 6px;
  margin-bottom: 35px;
  font-size: 1rem !important;
}

textarea {
  height: 5rem;
  max-height: 8rem;
  min-height: 4rem;
}

#submit-btn {
  background-color: #90d1ff;
  border: 2px solid #304e7e;
  color: black;
  padding: 12px 20px;
  cursor: pointer;
  font-size: 1rem;
}

#submit-btn:hover {
  background-color: #77c7ff;
}

/* When moving the mouse over the submit button, add a darker green color */
input[type="submit"]:hover {
  background-color: #77c7ff;
}

/* Add a background color and some padding around the form */
.container {
  width: 30rem !important;
  background-color: #f2f2f2;
  padding: 40px;
  margin: 0 auto;
}

.skills-text {
  text-decoration: none !important;
}

@media only screen and (max-width: 1100px) {
  .project-image {
    width: 22rem;
    height: 12rem;
  }

  .project-container {
    width: 22rem;
  }
}

@media only screen and (max-width: 980px) {
  #contact-info {
    margin-top: -4rem;
  }

  #contact-text {
    margin: 0 auto;
  }
}

@media only screen and (max-width: 780px) {
  .dropdown {
    display: inline-block;
  }

  .main-navigation {
    display: none;
  }
}

@media only screen and (max-width: 606px) {
  #index-main {
    margin: 1rem !important;
  }
}

@media only screen and (max-width: 715px) {
  #heading-info {
    flex-direction: column;
    margin-top: -4rem;
    width: 100%;
  }

  #heading-info img {
    margin: 2rem 0 0 0 !important;
  }

  main {
    margin-left: 0;
    margin-top: 10rem;
  }

  #main-text {
    margin: 0;
  }

  .mb-3 {
    margin: 0;
    margin-top: 2rem;
  }
}

@media only screen and (max-width: 510px) {
  project-image {
    height: auto;
    width: 15rem;
  }

  .container {
    width: 25rem !important;
    background-color: #f2f2f2;
    padding: 20px;
    margin: 0 auto;
    border: 1px solid #304e7e;
  }
}
